.review_body_container
{
    width: 100%;
    height:max-content;
    padding:10px 0 60px;
    margin:0;
    box-sizing: border-box;
    background-color:rgba(41, 185, 242, 0.671);
    background-color: #252C37;

    /* background: #0F2027;  /* fallback for old browsers */
    /* background: -webkit-linear-gradient(to top, #2C5364, #203A43, #0F2027);  Chrome 10-25, Safari 5.1-6 */
    /* background: linear-gradient(to top, #2C5364, #203A43, #0F2027); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-color: transparent;
}

.review_body_wrapper
{
    width:100%;
    height:max-content;
    padding: 20px 0px;
    margin: 0px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.container {
	 margin-top: 50px;
}
.columns
{
  display: grid;
  grid-template-columns:1fr 1fr 1fr;
  grid-template-rows: auto;
}
 .container .testimonial-wrapper {
	 padding: 20px;
	 transition: all 0.2s ease-in-out;
}
 .container .testimonial-wrapper:hover {
	 margin-top: -3px;
}
 .container .testimonial-wrapper .testimonial {
     background-color: white;
	 border-radius: 4px;
	 padding: 20px;
}
.container .testimonial-wrapper .testimonial .quote {
	 font-size: 1.3rem;
     color: #393846;
	 z-index: 1;
	 font-family: halyard-text, sans-serif;
	 font-weight: 100;
	 font-style: normal;
     letter-spacing: 0.02rem;
}
.container .testimonial-wrapper .testimonial .attribution {
	 color: #d78118;
	 text-transform: uppercase;
	 letter-spacing: 0.05rem;
	 padding-top: 15px;
	 font-family: poppins, sans-serif;
	 font-weight: 700;
	 font-size: 0.8rem;
	 font-style: normal;
}

@media screen and (max-width: 960px)
{

    .review_body_container
    {
        padding:0px;
    }
    .review_body_wrapper
    {
        padding:10px 10px;
    }

    .container{
      margin-top:0px;
    }
    .columns {
       margin-top: 50px;
       display: gird;
       grid-template-columns:1fr 1fr;
       grid-template-rows: auto;
    }

    .container .testimonial-wrapper .testimonial .quote {
        font-size : 1rem;
    }

}



@media screen and (max-width:660px)
{
    .columns {
       margin-top: 50px;
       display: gird;
       grid-template-columns: 1fr;
       grid-template-rows: auto;
    }
    .container .testimonial-wrapper .testimonial .quote {
        font-size: 1rem;
    }

}
