.why_container
{
    width: 100%;
    height: max-content;
    padding:10px 20px;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
    margin: 80px 0px 120px 0px !important;
 
}

.why_reason_body
{
    width: 100%;
    height:auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius:4px;
    overflow: hidden;
    padding:20px 10px;
    box-sizing: border-box;
    background-color: transparent;
    -webkit-box-shadow: 0px 2px 10px -1px rgba(84,192,215,1);
    -moz-box-shadow: 0px 2px 10px -1px rgba(84,192,215,1);
    box-shadow: 0px 2px 10px -1px rgba(84,192,215,1);
}

.why_reason_icon
{
    width: 70px;
    height: 70px;
    margin: auto;
}

.why_reason_text
{
    width: 100%;
    margin:0px;
    padding:16px;
    box-sizing: border-box;
    text-align: center;
    font-size: 1.6rem;
    font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
    font-weight: 500;
    /* font-weight: 600; */
    color: var(--fav);
}

@media screen and (max-width: 960px) {
	.why_container {
    grid-template-columns: 1fr 1fr 1fr;
    }


    .why_reason_icon {
    width:45px;
    height:45px;
    }


    .why_reason_text {
    font-weight: 500;
    font-size: 1.3rem;
    padding:10px;
    padding-bottom: 0px;
    }
}

@media screen and (max-width: 760px) {

    
.why_reason_body
{
    width: 100%;
    height:auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius:4px;
    overflow: hidden;
    padding:40px 10px;
    box-sizing: border-box;
    background-color: white;
    -webkit-box-shadow: none;
    -moz-box-shadow:none;
    box-shadow: none;
    border-bottom:solid 1px rgba(84,192,215,1);
}
	.why_container
{
    grid-template-columns: 1fr;
    grid-template-rows:1fr 1fr 1fr;
    grid-gap:0px;
}


.why_reason_icon
{
   width: 60px;
   height: 60px;
}


.why_reason_text
{
    font-weight: 500;
   font-size: 1.2rem;
   padding:10px;
   padding-bottom: 0px;
}
}
