@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');
@import url('https://fonts.googleapis.com/css?family=Righteous&display=swap');
.header_container
{
    width: 100%;
    margin:0px;
    padding:0px;
    box-sizing: border-box;
    background-color: #0e232e;
    background-color: #ffffff;
    -webkit-box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
    -moz-box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
    box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
}
.header_wrapper
{
  width: 100%;
  margin: 0px;
  padding:3px;
  box-sizing: border-box;
  display: flex;
}

.logo_wrapper
{
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.logo_text
{
   width: max-content;
   font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
   font-family: 'Righteous', cursive;
   line-height: 2.4rem;
   letter-spacing: 0.3rem;
   font-size: 2.6rem !important;
   font-weight: 900;
   color:#0e232e;
   margin:0px;
   padding:10px;
   box-sizing: border-box;
   margin-left: 20px;
}

.space
{
    width: 100%;
}

.space a {
    width: max-content !important;
    display: block !important;
    margin:0 !important;
    margin-left:25px !important;
    padding: 0px;
}
.space a:active {
    padding: 0;
}
.space a img
{
   width: 40px;
   height: 40px;
   margin:0 !important;
   padding:5px;
   border-radius:10px;
   overflow: hidden;
   background-color: #0e232e;
}

.space a img:hover
{
  background-color: #00a3d3;
  overflow: hidden;
  z-index:20;
}

.space a img:active {
    overflow: hidden;
    padding:5px;
    margin:0;
    transform: scale(1) !important;
}

.login_buttons_container
{
    width: max-content;
    margin: 0px;
    padding: 15px 10px;
    display: flex;
    margin-right: 20px;
    box-sizing: border-box;
}

.log_button
{
    width:max-content;
    font-size: 1.2rem;
    font-weight: 500;
    color: white;
    margin: 0px;
    padding-left:7px;
    padding-right: 5px;
    box-sizing: border-box;
    cursor: pointer;
}

.log_button button {
    background: #2193b0 !important;  /* fallback for old browsers */
    background-image: -webkit-linear-gradient(to right, #49a9be, #2193b0) !important;  /* Chrome 10-25, Safari 5.1-6 */
    background-image: linear-gradient(to right, #4faabe, #37a2bd) !important;
    color: white !important;
}
a {
    text-decoration: none;
}


.login_modal_box {
    width:max-content;
    outline: none;
    margin: auto;
    margin-top: 20vh;
    border: 0px;
}


@media screen and (max-width: 760px)
{
    .header_wrapper
    {
    width: 100%;
    margin: 0px;
    padding:3px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    }

    .logo_wrapper
    {
        width:100%;
        display: flex;
        flex-direction: row;
        border-bottom:solid 1px white;
        padding-bottom:10px;
    }

    .login_buttons_container
    {
    width:100%;
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
    }

    .log_button
    {
        width: 50%;
        display: inline-block;
        font-size: 1em;
        padding:0px 4px 4px 4px;
        margin:0px;
        box-sizing: border-box;
        text-align: center;
    }


    .log_button button
    {
        margin: 0px;
        width: 100%;
        font-size:1rem;
        padding:5px;
        border: 0px;
        border-radius: 2px;
    background-color: #364e59;
    }

    .active_button ,.log_button button:active
    {
        background: #235885;
        border: 0px;
        outline: none;
    }

    .space a
    {
        display: inline-block;
        margin-left:25px !important;
        margin-right: 30px !important;
        float: right !important;
    }

    .space a img
    {
        width: 40px;
        height: 40px;
        border-radius:10px;
        overflow: hidden;
    }

    .logo_text
    {
        margin-left: -5px;
        font-size: 1.6em;
        line-height: 1.5;
        font-weight: bold;
    }

}