.submit_page_why_wrapper {
    width: 100%;
    height: max-content;
    padding:100px 0px;
    margin:0px;
    box-sizing: border-box;
    /* background-image:linear-gradient(to bottom, rgba(0, 165, 165, 0.575), rgba(26, 116, 116, 0.575)),url('../home/components/projecter/besto_1000.jpg'); */
    background-size: 100% 100%;
    background-position : center top;
    background-repeat: no-repeat;
}
.info_heading_sub {
    width : 100%;
    margin : 10px 0px;
    padding :30px 0px;
    font-size: 1.5rem;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    font-weight: 300;
    box-sizing: border-box;
    color: tomato;
}
.submit_page_container {
    margin:0px 0px 80px 0px;
}

.submit_poster_wrapper {
    width: 100%;
    height: max-content;
    padding-left: 50px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}


.submit_poster_wrapper h1 {
    width: 100%;
	font-size: 4.2rem;
	font-family: 'Alfa Slab One', cursive;
	margin:0px;
    font-weight: 300;
    text-align:left;
}

.submit_poster_wrapper h2 {
    width: 100%;
	font-size: 3.9rem;
	font-family: 'Patua One', cursive;
	margin:0px;
	color:#0e232e;
	font-weight:500;
}



@media screen and (max-width: 760px) {
    .submit_page_why_wrapper {
        padding:10px 0px;
        /* background-image:linear-gradient(to bottom, rgba(0, 165, 165, 0.575), rgba(26, 116, 116, 0.575)),url('../home/components/projecter/besto_500.jpg'); */
        background-position: center;
    }
    .submit_poster_wrapper {
        padding:10px;
    }

    .submit_poster_wrapper h1 {
        font-size: 3.2rem;
        font-weight: 300;
    }
    
    .submit_poster_wrapper h2 {
        font-size: 2.6rem;
        font-weight:500;
    }

}
