:root {
	--red-color:#df4937;
	--green-color: #28634f;
	--blue-color: #4db3e9;
	--white-color: #F6F6F6;
	--black1-color: #444444;
	--black2-color: #2B2B2B;
	--fav:#576366;
}

._pricing_wrapper {
    display: flex;
	justify-content: space-around;
}

._plan_container {
    display: flex;
    flex-direction: column;
	width: 300px;
	height:400px;
	border-radius: 3px;
    border: solid 0px var(--fav);
    box-sizing: border-box;
    background-color: white;
    margin: 70px 0px;
    transition:300ms;
    justify-content: center;
	-webkit-box-shadow: 0px 8px 34px -5px rgba(54,78,89,1);
	-moz-box-shadow: 0px 8px 34px -5px rgba(54,78,89,1);
	box-shadow: 0px 8px 34px -5px rgba(54,78,89,1);
}

._plan_container:hover
{
	transform: translateY(-5px);
    z-index:20;
}

._plan_header {
    width: 100%;
	height: max-content;
    padding:20px 0 !important;
    text-align: center;
    box-sizing: border-box;
    margin:0px !important;

    font-family : "OpenSansRegular", Helvetica, sans-serif;
    font-size: 1.9rem !important;
    letter-spacing: 0.1rem;
    font-weight: 800;
    text-transform: uppercase;
    color: white !important;
}

._plan_detail {
    width: max-content;
    height: max-content;
    margin: auto;
    margin-top: 30px;
    box-sizing: border-box;
    display: flex;
    justify-items: center;
    flex-direction: column;
    padding: 0px !important;
}

._plan_detail li {
    width: 100%;
    list-style: none;
    display: flex;
    justify-items: center;
    box-sizing: border-box;
    padding: auto;
}

._plan_detail li h1 {
    font-size: 1.6rem;
    font-family : "OpenSansRegular", Helvetica, sans-serif;
    letter-spacing: 0.1rem;
    font-weight: 800;
    color: #252C37;
    font-style: normal;
    line-height: normal;
    padding:0px;
    box-sizing: border-box;
    margin: auto;
    background-color: transparent !important;
}


._plan_1 h1 ,._plan_1 button{
    background-color: var(--green-color) !important;
}
  
._plan_2 h1 ,._plan_2  button{
      background-color: var(--blue-color) !important;
}
  
._plan_3 h1 ,._plan_3 button{
      background-color: var(--red-color) !important;
}

._plan_pricing_box {
    margin:20px 0;
	font-family: 'Open Sans', sans-serif;
	font-weight: 600;
	font-size: 2.5rem;
	text-align: center;
    color: var(--black2-color);

}

._plan_pricing_box span {	
    width: max-content;
    margin: auto;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    font-size: 1.4rem;
    color: var(--black1-color);
}
._pricing_cta_link {
    margin: auto;
}


._plan_1 ._plan_pricing_box{
    color: var(--green-color) !important;
}
  
._plan_2 ._plan_pricing_box{
     color: var(--blue-color) !important;
}
  
._plan_3 ._plan_pricing_box{
     color: var(--red-color) !important;
     font-size: 1.7rem;
     padding:8px;
}

._pricing_cta {
    min-width:200px;
    margin: 2rem 0 !important;
    justify-self: center;
	align-self: center;
    border-radius: 2px;
    font-size: 1.6rem !important;
    font-family:"Gotham Rounded A", "Gotham Rounded B", Helvetica, Arial, sans-serif;
    font-weight: 500;
    padding:13px !important;
    color: var(--white-color) !important;
}
._pricing_cta:focus,
._pricing_cta:active,
._pricing_cta:hover {
    color: var(--blue-color);
	text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.8);
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 0.6;
}


._plan_1 button:focus,
._plan_1 button:active,
._plan_1 button:hover {
    color: var(--green-color);
}
  

._plan_2 button:focus,
._plan_2 button:active,
._plan_2 button:hover {
    color: var(--blue-color);
}

._plan_3 button:focus,
._plan_3 button:active,
._plan_3 button:hover {
    color: var(--red-color);
}


@media screen and (max-width:960px) {
	._pricing_wrapper  {
		display: flex;
		flex-wrap: wrap;
    }
    ._plan_container {
            width: 300px;
            margin:40px auto;
    }
}

@media screen and (max-width: 660px) {
	._pricing_wrapper {
		display: flex;
		flex-direction: column;
	}
}
