.checkout_container_outside {
  max-width: 600px;
  min-width:560px;
  height: max-content;
  box-sizing: border-box;
  padding:1rem;
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
  background-color:white;
  border:0;
  border-radius: 1px;
  -webkit-box-shadow: 0px 1px 3px 0.5px #00415d48;
  -moz-box-shadow: 0px 1px 3px 0.5px #00415d48;
   box-shadow: 0px 1px 3px 0.5px #00415d48;
}

.payment_checkout_into {
  width: 100%;
  margin: 0px;
  padding:0.5rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.payment_checkout_into h1,
.payment_checkout_into h2,
.payment_checkout_into h3 {
  width: 100%;
  font-family : "OpenSansRegular", Helvetica, sans-serif;
  letter-spacing: 0.1rem;
  margin:10px 0;
  font-weight: 400;
  text-align: center;
}

.country_selector {
  width: 100%;
  display: block;
  margin: 1.4rem 0;
  
  box-sizing: border-box;
  font-size: 1.2rem;
}

.payment_card_container {
  width: 100%;
  height: max-content;
  padding:1rem;
  margin: 1.5rem 0;
  border: solid 3px #6d6d6d;
  border-radius: 5px;
  background: #ECE9E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #FFFFFF, #ECE9E6);
}

.StripeElement {
    margin:20px 0px;
    box-sizing: border-box;
  
    max-height:60px;

    font-size:1.4rem;

    padding: 20px 12px;
  
    border: 1px solid transparent;
    border-radius: 4px;
    background-color: white;
  
    box-shadow: 0 1px 3px 0 #a0a2a5;
    -webkit-box-shadow: 0 1px 3px 0 #a0a2a5;
    -moz-box-shadow:0 1px 3px 0 #a0a2a5;
    -webkit-transition: box-shadow 150ms ease;
    transition: box-shadow 150ms ease;
  }
  
  .StripeElement--focus {
    box-shadow: 0 1px 3px 0 #6d6d6d;
  }
  
  .StripeElement--invalid {
    border-color: #fa755a;
  }
  
  .StripeElement--webkit-autofill {
    background-color: #fefde5 !important;
  }


@media screen and (max-width: 720px) { 
  .checkout_container_outside {
    margin-top: 100px !important;
    margin-bottom: 100px !important;
    max-width: 500px;
    min-width:460px;
  }
  .checkout_container_outside {
    padding:10px;
    margin: auto;
    border: 0px;
    border-radius: 0px;
}
}



@media screen and (max-width: 500px) { 
  .checkout_container_outside {
    min-width: 320px !important;
    margin:20px 10px !important;
  }
}