.account_container
{
    width: 100%;
    height: max-content;
    padding:10px;
    margin: 0px;
    box-sizing: border-box;
}
.account_wrapper
{
    width: 100%;
    height: max-content;
    padding:0px;
    margin: 0px;
    box-sizing: border-box;
    display: flex;
    justify-items: center;
    flex-direction: column;
}
.account_info_section
{
    width: 100%;
    height: max-content;
    padding:0px;
    margin: 0px;
    box-sizing: border-box;
    display: flex;
    justify-items: center;
    flex-direction: column;
}

.account_info_section img{
    width:200px;
    height: 200px;
    border-radius: 100%;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    -ms-border-radius: 100%;
    -o-border-radius: 100%;
    overflow: hidden;
    margin: auto;
}
.account_info_section h3
{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: center;
    font-size: 1.4em;
    font-family: 'Roboto', sans-serif;

}
.account_info_section h4
{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: center;
    font-size: 1.2em;
    font-family: 'Raleway', sans-serif;
}


.account_assignment_section_body
{
    width:100%;
    margin:20px 0px;
    padding:10px 30px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    overflow:hidden;
    box-sizing: border-box;
}

.account_assignment_section_wrapper
{
    width:100%;
    margin:0px;
    padding:0px;
    display: flex;
    flex-direction: column;
}


table {
    border: 0px solid rgb(218, 218, 218);
    border-collapse: collapse;
    margin: 0;
    padding: 0;
    width: 100%;
    table-layout: fixed;
    -webkit-box-shadow: 0 1px 8px 0 #d9dadb;
    -moz-box-shadow: 0 1px 8px 0 #d9dadb;
    box-shadow: 0 4px 8px 3px #d9dadb;
  }
  
  table caption {
    font-size: 1.7em;
    margin: .5em 0 .75em;
    font-family: 'Raleway', sans-serif;
  }
  
  table tr {
    border: 0px solid #ddd;
    padding: .35em;
  }
  
  table th,
  table td {
    padding: .625em;
    text-align: center;
    font-family: 'Roboto', sans-serif;
  }

  table td button {
    outline: none;
    background-color: rgb(6, 164, 255);
    font-size: 1rem;
    padding:8px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 3px;
    box-sizing: border-box;
    border: 0px;
  }
  
  table th {
    font-size: .85em;
    font-family: 'Roboto', sans-serif;
    letter-spacing: .1em;
    text-transform: uppercase;
  }

  .payment_model_box {
    width: 100%;
    height:100vh;
    background-color: white;
    overflow-x: scroll;
  }
  
  @media screen and (max-width: 720px) {
    table {
      border: 0;
    }
  
    table caption {
      font-size: 1.3em;
    }
    
    table thead {
      border: none;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px;
    }
    
    table tr {
      border-bottom: 3px solid #ddd;
      display: block;
      margin-bottom: .625em;
    }
    
    table td {
      border-bottom: 1px solid #ddd;
      display: block;
      font-size: .8em;
      text-align: right;
    }
    
    table td::before {
      /*
      * aria-label has no advantage, it won't be read inside a table
      content: attr(aria-label);
      */
      content: attr(data-label);
      float: left;
      font-weight: bold;
      text-transform: uppercase;
    }
    
    table td:last-child {
      border-bottom: 0;
    }
  }