.form_field_wrapper
{
    width: 100%;
    margin:0px;
    padding:5px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.form_field_label
{
    width:100%;
    font-size: 1.3rem;
    font-family:"Gotham Rounded A","Gotham Rounded B", Helvetica, Arial, sans-serif;
    font-weight: 400;
    padding:2px;
    margin:4px 0px 9px 0px;
    letter-spacing: .04em;
    font-weight: 100;
    color:#0e232e;
}

.form_field_input
{
    width: 100%;
    height:45px;
    outline: none;
    border: 1px solid #ddd;
    padding: 0 10px;
    border-radius: 0px;
    margin: 0px;
    font-size:1.1rem;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
    -webkit-transition:all 0.1s linear;
    -moz-transition:all 0.1s linear;
    transition:all 0.1s linear;
    color: black;
    background-color: white;
}

.form_field_input:focus {
    border:0px !important;
    box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -webkit-box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -moz-box-shadow: 0 1px 3px 0 #6d6d6d !important;
}

.form_field_error {
    font-size: 0.9rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 300;
    color:red;
    margin: 3px 0px;

}