._contact_form_container {
     width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    align-content: center;
}
._contact_form {
    max-width:600px;
    min-width:600px;
    height:max-content;
    box-sizing: border-box;
    padding:40px;
    display: grid;
    margin-top:40px;
    margin-bottom: 40px;
    grid-template-columns: 1fr;
    border-radius: 1px;
    overflow: hidden;
    background-color: white;
    -webkit-box-shadow: 0px 1px 9px 2px #00415d48;
    -moz-box-shadow: 0px 1px 9px 2px #00415d48;
     box-shadow: 0px 1px 9px 2px #00415d48; 
}