.login-box{
        margin: 10px auto;
        max-width:500px;
        min-height:300px;
        background-color: #fff;
        box-sizing: border-box;
        padding: 10px;
        -webkit-box-shadow: 0px 1px 3px 0.5px #00415d48;
       -moz-box-shadow: 0px 1px 3px 0.5px #00415d48;
        box-shadow: 0px 1px 3px 0.5px #00415d48;
        background-color: #364e59;
        background-color: white;
        outline: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
  }

  .login-box:hover {
        -webkit-box-shadow: 0px 1px 9px 2px #00415d48;
        -moz-box-shadow: 0px 1px 9px 2px #00415d48;
         box-shadow: 0px 1px 9px 2px #00415d48; 
  }

.lb-header{
        position:relative;
        color: #00415d;
        margin: 5px 5px 10px 5px;
        padding-bottom:20px !important;
        border-bottom: 1px solid #eee;
        text-align:center;
        display: grid;
        grid-template-columns: 1fr 1fr;
        height:28px;
        list-style: none;
}
.lb-header button {
        margin:0 25px 10px 25px;
        padding: 0 00px;
        text-decoration: none;
        color:#364e59;
        font-family:"SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
        cursor: pointer;
        background: transparent;
        border: 0px;
        outline: transparent;
        font-size: 1.2rem;
        -webkit-transition: all 0.2s;
        -moz-transition: all 0.2s;
        transition: all 0.2s;
}
.lb-header .active {
        color:#54c0d7;
        font-weight: 400;
        font-size: 1.5rem;
        background-color: transparent;
}

.u-form-group button {
        width:50%;
        background-color: #1CB94E;
        border: none;
        outline: none;
        color: #fff;
        font-size: 14px;
        font-family:"SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
        font-weight: normal;
        padding: 14px 0;
        border-radius: 2px;
        text-transform: uppercase;
}
.forgot-password {
        width:50%;
        text-align: left;
        text-decoration: underline;
        color: #888;
        font-size: 0.75rem;
        font-family:"SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
}

.log_message {
        width: 100%;
        margin:0px;
        padding:5px 0px;
        font-size: 0.9rem;
        text-align: center;
        font-family:"SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
        color: #c3423d;
}


.content_ed {
        width: 100%;
        box-sizing: border-box;
        height: max-content;
        display: flex;
        margin: auto;
}

.content_ed h6 {
        width: max-content;
        margin: auto;
        padding:30px;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: 600;
        font-size: 1.9rem;
        text-align: center;
}


.lables {
        margin: 20px 0px;
        padding:10px 0px;
        box-sizing: border-box;
        font-size: 1.2rem !important;
        font-weight: 300 !important;
}

.ses_message {
        font-size: 1.2rem !important;
        font-weight: 300 !important;
        width: 100%;
        text-align: center;
        color: red;
}

.inputs {
border-radius:0px !important;
padding:6px 20px !important;
font-size: 1.2rem !important;
}

.loading_icon {
        margin: auto !important;
        margin-top: 100px !important;
        margin-bottom: 100px !important;
}

@media screen and (max-width: 760px) {

    .login-box{
        width: 340px;
    }

    .content_ed h6 {
      font-weight: 500;
      font-size: 1.3rem;
    }
}
