.footer_container
{   width: 100%;
    height:max-content;
    padding:0px;
    margin:0px;
    margin-top:40px;
    border-top: 3px solid #54c0d7;
    box-sizing: border-box;
    background-color:#ffff;
    background: #0F2027;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: #2193b0;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #6dd5ed, #2193b0);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #6dd5ed, #2193b0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: #2193b0;  /* fallback for old browsers */
    background-image:url('../home/texture.jpg'), -webkit-linear-gradient(to right, #6dd5ed49, #2193b041);  /* Chrome 10-25, Safari 5.1-6 */
    background-image:url('../home/texture.jpg'), linear-gradient(to right, #6dd5ed46, #2193b026); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-size: 100% 100%;
    background-blend-mode: overlay;
	  background-position : center top;
    background-repeat: no-repeat;
}

.footer_wrapper {
  width:max-content;
  margin: auto;
  padding:30px;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
}

.footer_wrapper * {
  color:#ffff;
}

.footer_copy_wrapper {
  background-color:#54c0d7;
  padding:20px;
  box-sizing: border-box;
}

.footer_section {
    width: max-content;
    padding:20px;
    margin:10px 0px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.lables_ {
  margin:10px 0px 0px 0px !important;
  padding:0px !important;
  box-sizing: border-box;
  font-size: 1.2rem !important;
  font-weight: 400 !important;
  color:#ffff;
}

.inputs_ {
  width: 100%;
  border-radius:0px !important;
  border:0.5px solid rgb(0, 195, 255);
  padding:6px 20px !important;
  box-sizing: border-box;
  color : black !important;
  font-size: 1.2rem !important;
  margin:10px 0px!important;
}

.inputs_:focus {
  border:2px solid rgb(0, 195, 255);
}

@media screen and (max-width:960px) {
  .footer_container {
    padding:0px;
  }
    .footer_wrapper {
      width:100%;
      margin: auto;
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
    }
}



@media screen and (max-width:660px) {
  .footer_wrapper {
    padding:20px;
  }
  .footer_section {
    padding:20px 0px;
  }

  .lables_ {
    width:200px;
    margin:10px 0px !important;
    padding:0px !important;
  }

  .inputs_ { 
    width:300px;
    margin:10px 0px !important;
  }
  
}
