.submit_section_body{
    width:100%;
    height:max-content;
    box-sizing: border-box;
    margin: 0px;
    padding:20px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form_submit{
    max-width:600px;
    min-width:600px;
    height:max-content;
    box-sizing: border-box;
    padding:40px;
    display: grid;
    margin-top:40px;
    margin-bottom: 40px;
    grid-template-columns: 1fr;
    border-radius: 1px;
    overflow: hidden;
    background-color: white;
    -webkit-box-shadow: 0px 1px 9px 2px #00415d48;
    -moz-box-shadow: 0px 1px 9px 2px #00415d48;
     box-shadow: 0px 1px 9px 2px #00415d48; 
}

.submit_form_label_wrapper
{
    display: flex;
    flex-direction: column;
    margin-left:5px;
    margin-right: 5px;
}

label{
    font-size: 1.2rem;
    font-family:'Oswald', sans-serif;
    font-weight: 520;
    margin:20px 0px;
}
input,select{
    width: 100%;
    height:50px;
    font-size: 1.2rem;
    font-family: 'Open Sans', sans-serif;
    box-sizing: border-box;
    padding:5px;
    border:0px !important;
    background-color: white !important;
    font-size: 1.1rem !important;
    outline: none;
    border:0px !important;
    box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -webkit-box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -moz-box-shadow: 0 1px 3px 0 #6d6d6d !important;
}


.submit_form_label_wrapper input:focus,
.submit_form_label_wrapper select:focus,
.submit_form_label_wrapper textarea:focus {
    border:0px !important;
    box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -webkit-box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -moz-box-shadow: 0 1px 3px 0 #6d6d6d !important;
}

input[type='submit']{
    width:320px;
    background-color: #1CB94E;
    border: none;
    outline: none;
    color: #fff;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    font-weight: normal;
    padding: 14px 0;
    border-radius: 2px;
    text-transform: uppercase;
    margin: auto;
    margin-top: 20px;
      background-color: #364e59;
}

.dropzone{
        width:100%;
        height: 100px;
        cursor: pointer;
        border:solid 2px  #364e59;
        padding:20px;
        outline:none;
        background-color: transparent;
        background:url(./folder.svg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position-x: center;
        background-position-y: center;
        background-size:90px 200px;
        box-shadow: 0 1px 3px 0 #6d6d6d !important;
        -webkit-box-shadow: 0 1px 3px 0 #6d6d6d !important;
        -moz-box-shadow: 0 1px 3px 0 #6d6d6d !important;
}


textarea {
    width:100%;
    height:300px;
    padding: 12px 20px;
    box-sizing: border-box;
    border:0px;
    outline: none;
    border-radius: 4px;
    background-color: white;
    font-size: 1.2rem;
    font-family: 'Open Sans', sans-serif;
    box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -webkit-box-shadow: 0 1px 3px 0 #6d6d6d !important;
    -moz-box-shadow: 0 1px 3px 0 #6d6d6d !important;
  }

.error_message
{
  width:max-content;
  color: red;
  margin:auto;
  padding:10px;
  box-sizing: border-box;
  text-align: center;
  font-size: 1.2rem;
  font-family: 'Open Sans', sans-serif;
}

.auto_submit_button {
    width: 300px;
    outline: none;
    border: none;
    font-size: 1.4rem;
    padding:20px;
    box-sizing: border-box;
    margin: 40px auto 0px auto;
    background-color: #00a3d3;
    color: white;
    -webkit-box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
    -moz-box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
    box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
}

.auto_submit_button:hover {
  transition-duration: 200ms;
  background-color: #364e59;
  -webkit-box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
  -moz-box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
  box-shadow: 0px 5px 8px -2px rgba(54,78,89,1);
}

._contact_form_h1 {
    width: max-content;
    margin:10px 0px;
    font-size: 1.8rem;
    border-bottom: solid  2px rgba(54,78,89,1);
}

._contact_form_h2 {
    width: 100%;
    margin:20px 0px 50px 0px;
    font-size: 1.5rem;
    border-bottom:0px
}

.su_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    align-content: center;
}


@media screen and (max-width: 760px)
{
    .form_submit{
        min-width: 100%;
        padding:20px;
        box-sizing: border-box;
       display: flex;
       flex-direction: column;
    }
input,select{
width:100%;
}
input[type='submit']{
    width: 230px;
}
    .su_container {
        padding:30px 20px;
    }
}
