.langs_container
{
    width: 100%;
    margin:0px;
    padding:50px 0px;
    box-sizing: border-box;
    display: flex;
    padding: auto;
    flex-direction: row;
    overflow: scroll;
    background-color:transparent;
    background-size: contain;
    /* background: #0F2027;  /* fallback for old browsers */
    /* background: -webkit-linear-gradient(to top, #2C5364, #203A43, #0F2027);  Chrome 10-25, Safari 5.1-6 */
    /* background: linear-gradient(to top, #2C5364, #203A43, #0F2027); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   
   /* background-image: url(../projecter/contactbg.png);*/
}

.langs_container::-webkit-scrollbar {
   width:0px;
   height:0px;
 }
.lang_body
{
   width:110px;
   display: block;
   height: -webkit-max-content;
   height: -moz-max-content;
   height: max-content;
   padding:4px;
   margin:0px 50px;
   border-radius: 20px;
   background-color: white;
   box-sizing: border-box;
}
.lang_body img
{
    width:100px;
    height:100px;
    box-sizing: border-box;
    margin:auto;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}
.lang_body h4
{
   width: 100%;
   font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
   font-size: 1.2rem;
   font-weight: 300;
   text-align: center;
   margin:5px 0px 10px 0px;
   box-sizing: border-box;
   padding:auto 7px;
}


@media screen and (max-width: 760px)
{
   .langs_container
   {
      padding:20px 0px !important;
   }
   .lang_body {
      margin : 0px 30px;
   }
}


@media screen and (max-width: 460px)
{
   .langs_container
   {
      padding:10px 0px !important;
   }
   .lang_body {
      margin : 0px 20px;
   }
}
.home_container
{
    width:100%;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.home_title_wrapper {
    padding:40px 0;
    background: #ECE9E6; 
    background-image: -webkit-gradient(linear, left bottom, left top, from(#FFFFFF), to(#ECE9E6)); 
    background-image: linear-gradient(to top, #FFFFFF, #ECE9E6);    
}

.home_title_wrapper h1, 
.home_title_wrapper h6 {
    width: 100%;
    margin:0px;
    text-align: center;
    font-size: 2.5rem;
    font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
    font-family : "OpenSansRegular", Helvetica, sans-serif;
    font-weight: 700;
    letter-spacing: 0;
    color  : #364e59;
    padding:0px;

    font-style: normal;
    font-weight: 900;
    letter-spacing: 2px;
    line-height: normal;
    text-transform: uppercase;
    color: #252C37;
}

.home_title_wrapper h6 {
    font-size: 1.4rem;
    line-height: 1.42115;
    margin:20px 0 30px 0;
    font-weight: 400;
    letter-spacing: .012em;
    padding: 0px;
}
.different {
    background-color: rgba(41, 185, 242, 0.671);
    background-color: #252C37;
    background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left bottom, left top, from(#2C5364), color-stop(#203A43), to(#0F2027));
    background: linear-gradient(to top, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-image: linear-gradient(-225deg, #9EFBD3 0%, #57E9F2 48%, #45D4FB 100%);
    background: #2193b0;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background-image:url(/static/media/texture.5304b403.jpg), -webkit-gradient(linear, left top, right top, from(#6dd5ed46), to(#2193b026));
    background-image:url(/static/media/texture.5304b403.jpg), linear-gradient(to right, #6dd5ed46, #2193b026); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-size: 100% 100%;
    background-blend-mode: overlay;
	background-position : center top;
    background-repeat: no-repeat;
}

.triangle_1 , .triangle_2 , .triangle_dummy{
    width: 100%;
    height: 50px;
    margin:0px;
    clip-path: polygon(0 100%, 100% 100%, 100% 0);
    -webkit-clip-path: polygon(0 100%, 100% 100%, 100% 0);
    background-color: rgba(41, 185, 242, 0.671);
    background-color: #252C37;
}


.triangle_2 {
    -webkit-clip-path: polygon(0 0, 100% 100%, 0 100%);
    clip-path: polygon(0 0, 100% 100%, 0 100%);
    background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left bottom, left top, from(#2C5364), color-stop(#203A43), to(#0F2027));
    background: linear-gradient(to top, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}

.triangle_dummy {
    background-color: #ffffff00;
}

@media screen and (max-width: 760px)
{
    .home_title_wrapper h1 {
        font-size: 1.7rem;
        font-weight: 900;
    }
    .home_title_wrapper h6 {
        font-size: 1.4rem;
        margin:20px 0;
    }

    .triangle_1 , .triangle_2 , .triangle_dummy {
        height: 20px;
    }

}

